<template>
  <div class="home">
    <nav class="navbar">
      <form class="searchbar">
        <label>
          <span class='screen-reader-only'>Search:</span>
          <input v-model="tag" placeholder="Search for photos" type="text" class="searchbar-input">
        </label>
        <button type="submit" class="btn btn--green btn--go" @click.prevent="search">Search</button>
      </form>
    </nav>
    <div class="wrapper">
    <p v-if="loading" class="text-centered">Loading...</p>
    <ul class="image-card-grid" v-else>
      <image-card v-for="image in images" :key="image.id" :image="image"></image-card>
    </ul>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import ImageCard from '@/components/ImageCard.vue';
import config from '../../config';

export default {
  name: 'Home',
  components: {
    ImageCard,
  },
  data() {
    return {
      tag: '',
      loading: false,
      images: [],
    };
  },
  methods: {
    search() {
      this.loading = true;
      // this.console.log('Searching for: ', this.tag);

      this.getImages()
        .then((res) => {
          this.images = res.data.photos.photo;
          this.loading = false;
        })
        .catch((err) => {
          this.console.log(err);
        });
    },
    getImages() {
      return axios({
        method: 'get',
        url: 'https://api.flickr.com/services/rest',
        params: {
          method: 'flickr.photos.search',
          api_key: config.api_key,
          tags: this.tag,
          page: 1,
          per_page: 15,
          format: 'json',
          nojsoncallback: 1,
          extras: 'date_taken, date_upload, owner_name, tags,url_l, url_sq, url_t, url_s, url_q, url_m, url_n, url_z, url_c, url_l, url_o',
        },
      });
    },
  },

  metaInfo() {
    return {
      title: 'Flickr Search App - VueJs, Flickr Api',
      meta: [
        { name: 'description', content: 'Flickr Search App created using VueJs and Flickr Public photos api.' },

      ],
    };
  },

};
</script>

